/* css初始化样式 */
html, body, header, footer, h1, h2, h3, h4, h5, h6, ul, ol, p, input,
div {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  -webkit-tap-highlight-color: transparent;
  list-style: none;
  outline: none;
}
a{color: #fff;
}
a:link {color: #fff; text-decoration:none;
}
//未访问：蓝色、无下划线 
a:active {color:  #fff;
}
//激活：红色 
a:visited {color: #fff;text-decoration:none;
}
//已访问：purple、无下划线 
a:hover {color:  #fff; text-decoration:underline;
}
//鼠标移近：红色、下划线 
body {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  height: 100%;
  line-height: 1;
}
html,body{width: 100%;height: 100%;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
  outline: none;
}
input,textarea{border: none;-webkit-appearance: none;
}
input:focus,textarea:focus{outline-color: #fff;background: #fff;border: 0 solid #fff;
}
input:hover,textarea:hover{border: 0 solid #fff;
}
input:focus {
  outline: none;
}
.clear-float:after {
  display: block;
